@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR&family=Roboto&display=swap');

body { font-size: 16px; font-family: 'Noto Sans KR', sans-serif;
       font-family: 'Roboto', sans-serif; margin: 0;}
a { color: inherit; text-decoration: none }
ul, ol {list-style: none }
.row { max-width: 1300px; margin: 0 auto; padding: 0 20px }
.cf::after { content: ''; display: block; clear: both }
img { width: 100%; vertical-align: middle; }
button { cursor: pointer; border: none; font-family: 'Noto Sans KR', sans-serif;
        font-family: 'Roboto', sans-serif;}
fieldset { display: none }
legend { display: none }
h1, h2, h3, h4, h5, h6 { font-size:1 00%;}
table { width: 100%; border-collapse: collapse; table-layout: fixed }
.blind { position: absolute; width: 1px; height: 1px; 
         overflow:hidden; text-indent: -9999px; }
button { background: none; outline: none;}
